#sampleTable {
  position: relative;
  left: 0;
  width: 94%;
  min-width: 480px;
  margin: 0 auto 10px auto;
  font-size: 0.8rem;
  font-weight: bold;
  text-align: center;
  empty-cells: show;
  border-collapse: collapse;
  border: none; }

